@import "./src/common/styles/variables";
@import "./src/common/styles/mixins";

.label input[type=radio], .checked input[type=radio] {
  visibility: hidden;
  position: absolute;
}

.label, .checked {
  display: inline-block;
  cursor: pointer;
  margin: 10px;
}

.label:before {
  content: '';
  display: inline-block;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #bdbdbd;
  border-radius: 50%;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.checked:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 50%;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  box-sizing: border-box;
  border: 5px solid $BtnMainTextColor;
}

