@import "./src/common/styles/variables";
@import "./src/common/styles/mixins";

.default {
  text-decoration: none;
  display: inline-block;
  min-width: 140px;
  height: 45px;
  line-height: 45px;
  border: white;
  border-radius: 45px;
  margin: 10px 20px;
  padding: 0 20px;
  font-family: 'Montserrat', sans-serif;
  font-size: 11px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3px;
  font-weight: 600;
  color: $BtnMainTextColor;
  background: $BtnMainBgColor;
  box-shadow: 0 8px 15px $BtnColorShadow;
  transition: .3s;

  &:hover { //color change places
    background: $BtnMainTextColor;
    box-shadow: 0 15px 20px $BtnActiveShadow;
    color: $BtnMainBgColor;
    transform: translateY(-7px);
  }

  &:disabled {
    transform: translateY(0px);
    color: $BtnDisabledTextColor;
    background: $BtnDisabledBgColor;

    &:hover {
      animation-name: wobble;
      -webkit-animation-duration: 0.8s;
      -webkit-animation-iteration-count: 1;
      -webkit-animation-timing-function: linear;
      background: $BtnDisabledBgColor;
      box-shadow: none;
    }
  }

}

.red {
  color: $BtnCancelTextColor;

  &:hover {
    background: $BtnCancelBgColor;
    box-shadow: 0 15px 20px $BtnCancelColorShadow;
  }
}

.active {
  background: $BtnMainTextColor;
  color: $BtnMainBgColor;
}


@keyframes wobble {
  0% {
    -webkit-transform: none;
    transform: none;
  }

  15% {
    -webkit-transform: translate3d(-3%, 0, 0) rotate3d(0, 0, 1, -2deg);
    transform: translate3d(-3%, 0, 0) rotate3d(0, 0, 1, -2deg);
  }

  45% {
    -webkit-transform: translate3d(3%, 0, 0) rotate3d(0, 0, 1, 2deg);
    transform: translate3d(3%, 0, 0) rotate3d(0, 0, 1, 2deg);
  }

  60% {
    -webkit-transform: translate3d(0%, 0, 0) rotate3d(0, 0, 1, -2deg);
    transform: translate3d(0%, 0, 0) rotate3d(0, 0, 1, -2deg);
  }

  75% {
    -webkit-transform: translate3d(3%, 0, 0) rotate3d(0, 0, 1, 1deg);
    transform: translate3d(3%, 0, 0) rotate3d(0, 0, 1, 1deg);
  }

  100% {
    -webkit-transform: none;
    transform: none;
  }
}



